import Swal from 'sweetalert2';

const Alerts = {
  showLoading() {
    Swal.fire({
      title: 'Memuat',
      html: `<div class="spinner-border text-primary" role="status">
      <span class="sr-only"><img src="./images/loading.svg"/></span></div>`,
      allowOutsideClick: false,
      allowEscapeKey: false,
      showConfirmButton: false,
      backdrop: false,
    });
  },

  closeLoading() {
    Swal.close();
  },

  showError(message) {
    Swal.fire({
      icon: 'error',
      title: 'Oops...',
      text: message,
      backdrop: false,
    });
  },
};

export default Alerts;
